	module.exports = {"data":{"site":{"siteMetadata":{"title":"Annalisa Valente"}},"contentfulPageSelectorPanel":{"pages":[{"name":"IBM ","title":"IBM","slug":"/projects/IBM/","description":"Product designer in the product team of IBM AIOps"},{"name":"Nectar ","title":"Nectar","slug":"/projects/nectar/","description":"  New way of discovering Nectar brands for web and iOS-Android "},{"name":"The Telegraph puzzle","title":"The Telegraph","slug":"/projects/the-telegraph/","description":"Working on various digital newspaper touch points"},{"name":"Google","title":"Google","slug":"/projects/google/","description":"Interaction Designer Contractor in the Google Play Console team"},{"name":"PD Spellbook","title":"PD Spellbook","slug":"/projects/pd-spellbook","description":"Glossary of terms for product people, side project"},{"name":"Mindfulness Moments","title":"Mindfulness Moments","slug":"/projects/mindfulness-moments/","description":"Building a new fundraising proposition"},{"name":"Mighty Hikes","title":null,"slug":"/projects/mighty-hikes","description":"Building a digital fundraising proposition experience"},{"name":"Lloyds Banking Group","title":"Lloyds Banking Group","slug":"/projects/lloyds-banking-group","description":"Consultant in the Personalised interactions, Mobile team"},{"name":"Local Cancer Intelligence","title":"Local Cancer Intelligence","slug":"/projects/local-cancer-intelligence/","description":"New LCI website"},{"name":"MyMail & Nectar","title":"MyMail & Nectar","slug":"/projects/mymail-and-nectar/","description":"Launch of MyMail and Nectar proposition"},{"name":"Calvin Klein","title":"Calvin Klein","slug":"/projects/calvin-klein/","description":"Design a microsite for Calvin Klein Italy"},{"name":"Emoji and depression","title":null,"slug":"/projects/emoji-and-depression","description":"Detect depression in young people "}]},"contentfulPage":{"slug":"/projects/lloyds-banking-group","password":null,"panels":[{"__typename":"ContentfulContentPanel","content":{"content":">__My role:__ Senior UX Designer\n> \n>__Timeframe:__ 6 month\n\n\n","childMarkdownRemark":{"html":"<blockquote>\n<p><strong>My role:</strong> Senior UX Designer</p>\n<p><strong>Timeframe:</strong> 6 month</p>\n</blockquote>"}}},{"__typename":"ContentfulContentPanel","content":{"content":"__Business requirements__\nAt Lloyds I played a role in shaping the project requirements by ensuring that a customer centric approach was adopted.\n\nI did this by producing high-level flows for many features that are now live in the mobile app.\nSome feature that we released: \n - Chat bot\n - Geolocation \n\n__Approach__\nI adopted a design thinking approach working with various stakeholders developing features in a two weeks sprint. I worked collaboratively with various business areas to collect data for research, with both Android and iOS dev team supporting the build and iterations.\n\n__Research__ We conducted research talking to people that use the app and we collected data that we had to identify design opportunities and to validate hypotesis.\n We also set up clear KPIs upfront to measure design success.\n \n__Users__ \nLloyds Active app Users are people from 23 to 37 years old. The most active population group by age are people between 18-25 years old with 72.30% of active users.\n\n__Challenges__\nSince Lloyds is one of the biggest bank in the UK, I've had to align different stakeholders from different business area, I did this organising design thinking activities, like workshops or Prioritization Matrices and other techniques.\n","childMarkdownRemark":{"html":"<p><strong>Business requirements</strong>\nAt Lloyds I played a role in shaping the project requirements by ensuring that a customer centric approach was adopted.</p>\n<p>I did this by producing high-level flows for many features that are now live in the mobile app.\nSome feature that we released: </p>\n<ul>\n<li>Chat bot</li>\n<li>Geolocation </li>\n</ul>\n<p><strong>Approach</strong>\nI adopted a design thinking approach working with various stakeholders developing features in a two weeks sprint. I worked collaboratively with various business areas to collect data for research, with both Android and iOS dev team supporting the build and iterations.</p>\n<p><strong>Research</strong> We conducted research talking to people that use the app and we collected data that we had to identify design opportunities and to validate hypotesis.\nWe also set up clear KPIs upfront to measure design success.</p>\n<p><strong>Users</strong>\nLloyds Active app Users are people from 23 to 37 years old. The most active population group by age are people between 18-25 years old with 72.30% of active users.</p>\n<p><strong>Challenges</strong>\nSince Lloyds is one of the biggest bank in the UK, I've had to align different stakeholders from different business area, I did this organising design thinking activities, like workshops or Prioritization Matrices and other techniques.</p>"}}},{"__typename":"ContentfulImagePanel","fullWidth":true,"caption":"Example of a feature","media":{"description":"","id":"c6zTnnun2sQaab1UH3qbrJ3","file":{"url":"//images.ctfassets.net/wk0hltq7eofw/6zTnnun2sQaab1UH3qbrJ3/3a5b917d22938e8c4ac9d03f2a30e2bf/Screen_Shot_2020-01-26_at_21.54.22.png"}}}]}},"pathContext":{"slug":"/projects/lloyds-banking-group","p":"35675e68f4b5af7b995d9205ad0fc43842f16450"}}

